    import {
        mapState
    } from 'vuex'

    export default {
        computed: mapState(['hasLogin', 'userName']),
        onLoad() {
			/**
			 * 如果没有登录，直接跳转到登录页面
			 */
            if (!this.hasLogin) {
				//reLaunch关闭所有的页面，跳转
                uni.reLaunch({
                    url: '../login/login'
                });
            } else {
				
			}
        }
    }
